export function pipe(...fns) {
  return (arg) => fns.reduce((prev, fn) => fn(prev), arg);
}
export function pipeWith(arg, ...fns) {
  return pipe(...fns)(arg);
}


export const missingArgs = <T = any>(...params: T[]) => params.some(param => isUndefined(param));

